import React, {FC} from 'react';

import './Card.scss';

interface ICardProps {
    name: string;
    imageHrefs: string[];
    path: string;
    maxHeight?: string | number;
}

const setPath = (path: string) => {
    window.location.hash = path;
}

const Card: FC<ICardProps> = (props) => {
    //const maxHeight = props.maxHeight ?? "30em";
    return (
        <div className="card" onClick={() => setPath(props.path)}>
            <h2 style={{gridArea: "name"}}>{props.name}</h2>
            <p style={{gridArea: "content"}}>{props.children}</p>
            <div className="images">
                {props.imageHrefs.map(href => {
                    return <img key={href} src={href} />
                })}
            </div>
        </div>
    )
}

export default Card;