import React, { FC } from "react";
import { Link } from "react-router-dom";
import './Header.scss';


const Header: FC = () => {
    
    return (
      <div className="header-container">          
        <div className="header">    
            <h1 style={{gridArea: "name"}}><a href="#/" >Chris Dirkis</a></h1>
            <p style={{gridArea: "email"}}>email: <a href="mailto:me@chrisdirk.is">me@chrisdirk.is</a></p>
            <p style={{gridArea: "twitter"}}>twitter: <a href="https://twitter.com/chrisdirkis">@chrisdirkis</a></p>
        </div>
        <div className="stripes">
            <div className="s3" />
            <div className="s2" />
            <div className="s1" />
        </div>
      </div>
    );
    

}

export default Header;